<template>
  <div>
    <!-- <div id="app">{{ info }}</div> -->

    <!-- <div id="app">
      <ol>
        <li v-for="i in info" :key="i">{{ i.name }}</li>
      </ol>
    </div>-->

    <div>
      <p v-for="i in info" :key="i">
        <img width="200" height="200" :src="require('../../../static/img/'+i.photopath)" />
        <!-- <img width="200" height="200" src="/static/img/" +{{i.photopath}} /> -->

        <span>{{i.name}}</span>
        <span>{{i.jianjie}}</span>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      info: null
    };
  },
  mounted() {
    // debugger;
    this.$axios
      .get("http://192.168.11.140/api/values/json")
      .then(response => (this.info = response.data))
      .catch(function(error) {
        // 请求失败处理
        console.log(error);
      });
  }
};
</script>

<style>
</style>


